<template>
  <div class="container">
    <van-nav-bar
      :title="`购物车(${totalNum})`"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="list-container">
      <van-pull-refresh v-model="isLoading" @refresh="refresh">
        <van-empty description="购物车无内容" v-if="cartList.length === 0">
          <van-button
            round
            type="danger"
            @click="$router.push('/')"
            class="bottom-button"
          >
            立即购物
          </van-button>
        </van-empty>
        <van-swipe-cell
          v-for="(
            { proid, num: cartNum, originprice, proname, img1, cartid }, index
          ) in cartList"
          :key="proid"
        >
          <div class="cell-left">
            <van-checkbox
              @click="handleSelectOne(cartid, cartList[index].flag)"
              v-model="cartList[index].flag"
            ></van-checkbox>
            <van-card
              tag="标签"
              :num="cartNum"
              :price="originprice"
              :title="proname"
              :thumb="img1"
              :thumb-link="`/#/details/${proid}`"
            >
              <!-- <template #left> left </template> -->
              <template #num>
                <van-stepper
                  v-model="cartList[index].num"
                  theme="round"
                  :name="cartid"
                  button-size="22"
                  disable-input
                  @change="handleChangeGoodNum"
                />
              </template>
            </van-card>
          </div>
          <template #right>
            <van-button
              square
              text="删除"
              @click="handleDeleteGood(cartid)"
              type="danger"
              class="delete-button"
            />
          </template>
        </van-swipe-cell>
      </van-pull-refresh>

      <van-divider>可能你还想要</van-divider>

      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getRecommendlist"
      >
        <GoodsList ref="goodList" :goodsList="recommendList" />
      </van-list>
      <van-submit-bar
        :price="totalPrice"
        button-text="去结算"
        @submit="onSubmit"
      >
        <van-checkbox @click="handleSelectAll" v-model="checked">
          全选
        </van-checkbox>
        <!-- <template #tip>
          你的收货地址不支持同城送,
          <span @click="onClickEditAddress">修改地址</span>
        </template> -->
      </van-submit-bar>
    </div>
  </div>
</template>

<script>
/**
 * 全选功能实现思路：
 * 1. 商品列表的item-list添加van-checkbox，绑定的值为item.flag
 * 2. 每个check-box绑定click事件用于发送单个选中状态change的请求给后端（如果使用change，全选按钮值变化导致单个checkbox改变的时候也会触发）
 * 3. 全选checkbox的绑定一个computed属性：get返回cartlist的every（判断flag是否全部为true），所以单个checkbox发生变化也会影响这个computed属性，set：遍历cartlist，每个item的flag变为true
 * 4. 全选checkbox绑定一个click事件，用于向后端发送changeall的请求
 */
import {
  cartRemove,
  cartUpdatenum,
  cartUpdateSelectone,
  cartUpdateSelectall,
  cartGetRecommendlist
} from '@/api/cart'
import GoodsList from '@/components/GoodsList.vue'
import { createNamespacedHelpers, mapMutations } from 'vuex'
const { mapState } = createNamespacedHelpers('user')
const cart = createNamespacedHelpers('cart')
export default {
  name: 'CartView',
  components: {
    GoodsList
  },
  data() {
    return {
      isLoading: false,
      recommendlistCount: 1,
      recommendList: [],
      // 用于下拉数据
      loading: false,
      finished: false
    }
  },
  created() {
    // 获取购物车列表数据
    this.getCartListData(this.userid)
    // 获取猜你喜欢数据
    this.getRecommendlist()
  },
  computed: {
    ...mapState(['userid']),
    ...cart.mapState(['cartList']),
    totalNum() {
      return this.cartList
        .filter((item) => item.flag)
        .reduce((curren, next) => {
          return curren + next.num
        }, 0)
    },
    totalPrice() {
      return (
        this.cartList
          .filter((item) => item.flag)
          .reduce((curren, next) => {
            return curren + next.originprice * next.num
          }, 0) * 100
      )
    },
    checked: {
      get() {
        return this.cartList.every((item) => item.flag)
      },
      set(val) {
        console.log(val)
        this.cartList.forEach((element) => {
          element.flag = val
        })

        // this.$forceUpdate();
      }
    }
  },
  methods: {
    ...cart.mapActions(['getCartListData']),
    onSubmit() {
      // 提交订单要做什么：1. 跳转到订单页面, 2. 已选中信息保存到本地（sessionStorage）
      // 订单页面展示什么：2. 商品信息
      sessionStorage.setItem(
        'selectedCartList',
        JSON.stringify(this.cartList.filter((item) => item.flag))
      )
      this.$router.push('/order')
    },
    // 使用name绑定元素的cartid
    handleChangeGoodNum(value, { name }) {
      cartUpdatenum({
        cartid: name,
        num: value
      }).then((res) => {
        if (res.code === '200') {
          console.log('change', value)
          // 如果想要异步执行，在确认后端更新数据后前端展示数据变化可以使用下面的代码，
          // 但需要注意，value的变化会再次导致函数的执行，可以在上层做一次value是否相等的判断，以减少重复请求
          // this.cartList.find((item) => item.cartid === name).num = value;
        }
      })
    },
    onClickLeft() {
      this.$router.go(-1)
    },
    refresh() {
      this.getCartListData(this.userid).then(() => {
        this.isLoading = false
      })
    },
    getRecommendlist() {
      this.loading = true
      cartGetRecommendlist({
        count: this.recommendlistCount++
      })
        .then((res) => {
          console.log('***', res)
          if (res.code === '200') {
            this.loading = false
            if (res.data.length === 0) {
              this.finished = true
              return false
            }
            this.recommendList = res.data
          }
        })
        .catch((err) => console.error(err))
    },
    handleDeleteGood(cartid) {
      // 使用异步关闭，在关闭之前，删除元素
      this.$dialog.confirm({
        message: '确认删除？',
        beforeClose: (action, done) => {
          if (action === 'confirm') {
            cartRemove({
              cartid
            })
              .then((res) => {
                if (res.code === '200') {
                  // 后端删除成功后前端数据对应展示，减少一次数据刷新请求
                  this.cartList.splice(
                    this.cartList.findIndex((item) => item.cartid === cartid),
                    1
                  )
                  done()
                }
              })
              .catch((err) => console.error(err))
          } else {
            done()
          }
        }
      })
    },
    // click可以避免二次触发，这里需要注意
    handleSelectOne(cartid, flag) {
      cartUpdateSelectone({
        cartid,
        flag
      })
        .then((res) => {
          console.log(res)
        })
        .catch((err) => console.error(err))
    },
    handleSelectAll() {
      cartUpdateSelectall({
        userid: this.userid,
        type: this.checked
      })
        .then((res) => {
          console.log('checkall', res)
        })
        .catch((err) => console.error(err))
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: yellowgreen;

  .list-container {
    flex: 1;
    .van-swipe-cell {
      .cell-left {
        display: flex;
        .van-checkbox {
          width: 80px;
          box-sizing: border-box;
          padding-left: 25px;
        }
        .van-card {
          flex: 1;
        }
      }
    }
  }
  .goods-card {
    margin: 0;
    background-color: #fff;
  }

  .delete-button {
    height: 100%;
  }
}
</style>
