<template>
  <div class="search-list-container">
    <van-cell :title="title" value="隐藏" size="large" />
    <div class="tagBox">
      <van-tag
        color="#efefef"
        text-color="tomato"
        v-for="item in dataList"
        :key="item.wordid"
        @click="handleSearchItemClick(item.keyword)"
      >
        {{ item.keyword }}
      </van-tag>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    dataList: {
      type: Array,
      required: true
    },
    title: String
  },
  methods: {
    handleSearchItemClick(keyword) {
      this.$parent.handleSearchItemClick(keyword)
    }
  }
}
</script>

<style lang="stylus" scoped>
.tagBox
  display flex
  flex-wrap wrap
  padding 0 10px
  .van-tag
    padding 3px 5px
    margin 10px
</style>
