<template>
  <div class="home">
    <ComHeader />
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <div class="banner" v-if="bannerList.length > 0">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item
            v-for="{ bannerid, img, alt } in bannerList"
            :key="bannerid"
          >
            <img :src="img" :alt="alt" />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="nav">
        <van-grid column-num="5" icon-size="35" clickable :border="false">
          <van-grid-item
            class="temp"
            v-for="item in gridList"
            :key="item.id"
            :icon="item.url"
            :text="item.text"
            @click="handleGriditemClick(item.id)"
          />
        </van-grid>
      </div>
      <div class="miaosha">
        <div class="miaosha-header">
          <h3 class="miaosha-title">京东秒杀</h3>
          <div class="miaosha-hour">{{ hour | format }}点场</div>
          <van-count-down :time="time" format="DD 天 HH 时 mm 分 ss 秒">
            <template #default="timeData">
              <span class="block">{{ timeData.hours | format }}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.minutes | format }}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.seconds | format }}</span>
            </template>
          </van-count-down>
        </div>
        <div class="miaosha-content">
          <div
            class="content-item"
            v-for="item in miaoshaList"
            :key="item.proid"
          >
            <img :src="item.img1" alt="" />
            <p class="price">${{ item.originprice }}</p>
          </div>
        </div>

        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="getGoodsData"
        >
          <GoodsList ref="goodList" :goodsList="goodsList" />
        </van-list>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
// @ is an alias to /src
import { getBannerList, getSecondKillGoods, getGoodsList } from '../api/home'
import GoodsList from '../components/GoodsList.vue'
import ComHeader from '../components/ComHeader.vue'

export default {
  name: 'HomeView',
  components: {
    GoodsList,
    ComHeader
  },
  data() {
    return {
      bannerList: [],
      gridList: [
        {
          id: this.$nanoid(),
          text: '京东超市',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/175540/24/19329/6842/60ec0b0aEf35f7384/ec560dbf9b82b90b.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '数码电器',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/178015/31/13828/6862/60ec0c04Ee2fd63ac/ccf74d805a059a44.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东新百货',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/54043/33/19389/4660/62b049dbE3b9aef75/2fcd31afd5d702e4.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东生鲜',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/177902/16/13776/5658/60ec0e71E801087f2/a0d5a68bf1461e6d.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东到家',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/196472/7/12807/7127/60ec0ea3Efe11835b/37c65625d94cae75.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东超市',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/175540/24/19329/6842/60ec0b0aEf35f7384/ec560dbf9b82b90b.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '数码电器',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/178015/31/13828/6862/60ec0c04Ee2fd63ac/ccf74d805a059a44.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东新百货',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/54043/33/19389/4660/62b049dbE3b9aef75/2fcd31afd5d702e4.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东生鲜',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/177902/16/13776/5658/60ec0e71E801087f2/a0d5a68bf1461e6d.png!q70.jpg'
        },
        {
          id: this.$nanoid(),
          text: '京东到家',
          url: 'https://m15.360buyimg.com/mobilecms/jfs/t1/196472/7/12807/7127/60ec0ea3Efe11835b/37c65625d94cae75.png!q70.jpg'
        }
      ],
      miaoshaList: [],
      time: 0,
      hour: 0,
      isLoading: false,
      goodsList: [],
      currentCount: 1,
      // 用于下拉数据
      loading: false,
      finished: false
    }
  },

  filters: {
    format(num) {
      return num < 10 ? '0' + num : num
    }
  },
  created() {
    // 获取秒杀商品当前剩余时间
    this.getRemainTime()
    getBannerList()
      .then((res) => {
        this.bannerList = res.data
      })
      .catch((err) => {
        console.error(err)
      })
    getSecondKillGoods()
      .then((res) => {
        this.miaoshaList = res.data
      })
      .catch((err) => {
        console.error(err)
      })
  },
  mounted() {
    // this.getGoodsData();
  },
  methods: {
    handleGriditemClick(arg) {
      // this.$router.push({ name: "details", params: { id: arg } });
      console.log(arg)
    },
    getRemainTime() {
      const date = new Date()
      let hour = date.getHours()
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      let day = date.getDate()
      if (hour % 2 === 0) {
        hour += 2
      } else {
        hour += 1
      }
      // 用于显示几点场
      this.hour = hour - 2
      if (hour === 24) {
        hour = 0
        day += 1
        // 你要是不知道13578十腊，我看你这个ios兼容怎么写
        if (
          (day === 31 && [1, 3, 5, 7, 8, 10, 11].indexOf(month) === -1) ||
          (day === 32 && [1, 3, 5, 7, 8, 10, 11].indexOf(month) !== -1)
        ) {
          day = 1
          month += 1
          if (month === 13) {
            month = 1
            year += 1
          }
        }
      }
      // 计算剩余时间的数字
      this.time =
        new Date(`${year}/${month}/${day} ${hour}:00:00`).getTime() -
        date.getTime()
    },
    getGoodsData() {
      console.log(this.currentCount)
      this.loading = true
      return getGoodsList(this.currentCount++)
        .then((res) => {
          this.loading = false
          if (res.data.length === 0) {
            this.finished = true
            return false
          }
          this.goodsList = this.goodsList.concat(res.data)
        })
        .catch((err) => {
          console.error(err)
        })
    },
    onRefresh() {
      this.currentCount = 1
      this.goodsList = []
      this.getGoodsData().then(() => {
        this.isLoading = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  background: #f6f6f6;
  overflow: hidden;

  .banner {
    box-sizing: border-box;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    .my-swipe .van-swipe-item {
      color: #fff;
      font-size: 20px;
      text-align: center;
      background-color: #39a9ed;
      img {
        display: block;
        width: 100%;
      }
    }
  }
  .nav {
    ::v-deep .van-grid-item__content {
      background: transparent;
      padding: 16px 7px;
    }
  }
  .miaosha {
    margin: 10px;
    border-radius: 15px;
    overflow: hidden;
    background: #fff;
    padding: 10px 0 5px;
    .miaosha-header {
      display: flex;
      align-items: center;
      margin: 0px 0 10px;
      height: 40px;
      line-height: 42px;
      font-size: 15px;

      .miaosha-title {
        font-size: 17px;
        font-weight: bolder;
        width: 150px;
        height: 100%;
        text-align: center;
        padding-left: 5px;
      }
      .miaosha-hour {
        color: #ee0a24;
        font-weight: bold;
        height: 100%;
        margin-right: 15px;
      }
      .van-count-down {
        width: 176px;
        // background: #39a9ed;
        transform: translateY(-3px);
        .colon {
          display: inline-block;
          margin: 0 4px;
          color: #ee0a24;
        }
        .block {
          display: inline-block;
          width: 45px;
          height: 28px;
          line-height: 32px;
          font-weight: bold;
          border-radius: 5px;
          color: #fff;
          text-align: center;
          background-color: #ee0a24;
        }
      }
    }
    .miaosha-content {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      box-sizing: border-box;
      .content-item {
        width: 100px;
        img {
          width: 100%;
          height: 100px;
        }
        .price {
          // background: orange;
          margin: 5px auto 5px;
          font-weight: bold;
          color: #f2270c;
          text-align: center;
        }
      }
    }
  }
}
</style>
