<template>
  <div class="container">
    <van-field v-model="password" type="password" label="密码" />
    <van-button
      @click="handleFinished"
      type="danger"
      :disabled="!btnFinishFlag"
      round
      block
      >完成</van-button
    >
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("login");
import { dofinishregister } from "@/api/user";
export default {
  data() {
    return {
      password: "",
    };
  },
  methods: {
    handleFinished() {
      // tel: 17512345678
      // password: 123456qW!
      dofinishregister({
        tel: this.tel,
        password: this.password,
      })
        .then((res) => {
          console.log(res);
          if (res.code === "200") {
            this.$dialog
              .alert({
                message: "注册成功，立刻去登陆？",
              })
              .then(() => {
                // on close
                this.$router.go(-3);
              });
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  computed: {
    ...mapState(["tel"]),
    btnFinishFlag() {
      // 至少6位，包含：数字，大写字母，小写字母，特殊符号
      return /^\S*(?=\S{6,})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/.test(
        this.password
      );
    },
  },
};
</script>

