import {
  getCartList,
} from "@/api/cart";
export default {
  // 不定义命名空间会导致同一个mutation在每个module下触发
  namespaced: true,
  state: {
    tel: '',
    cartList: []
  },
  mutations: {
    setTel(state, payload) {
      state.tel = payload
    },
    updateCartList(state, payload) {
      state.cartList = payload
    }
  },
  getters: {
    totalNum(state) {
      return state.cartList
        .reduce((curren, next) => {
          return curren + next.num;
        }, 0);
    },
    // totalPrice(state) {
    //   return (
    //     state.cartList
    //     .filter((item) => item.flag)
    //     .reduce((curren, next) => {
    //       return curren + next.originprice * next.num;
    //     }, 0) * 100
    //   );
    // },
  },
  actions: {
    getCartListData({
      commit
    }, payload) {
      return new Promise((resolve, reject) => {
        getCartList({
            userid: payload,
          })
          .then((res) => {
            // this.isLoading = false; 组件内部去处理
            if (res.code !== "10020") {
              commit('updateCartList', res.data);
            }
            resolve()
          })
          .catch((err) => {
            console.error(err);
            reject()
          });
      })
    },
  }
}