import nanoid from "./nanoid";
import {
  Swipe,
  SwipeItem,
  Button,
  Grid,
  GridItem,
  CountDown,
  PullRefresh,
  NavBar,
  List,
  Cell,
  Toast,
  Icon,
  Popover,
  ShareSheet,
  Tag,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Popup,
  ImagePreview,
  Field,
  Dialog,
  Form,
  Notify,
  Empty,
  Card,
  SwipeCell,
  Stepper,
  SubmitBar,
  Checkbox,
  Divider,
  Badge,
  Tabbar,
  TabbarItem,
  Search,
  ContactCard,
  AddressList,
  AddressEdit,
  CellGroup,
} from "vant";

const components = [
  Swipe,
  SwipeItem,
  Button,
  Grid,
  GridItem,
  CountDown,
  PullRefresh,
  NavBar,
  List,
  Cell,
  Toast,
  Icon,
  Popover,
  ShareSheet,
  Tag,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Popup,
  Field,
  Dialog,
  Form,
  Notify,
  Empty,
  Card,
  SwipeCell,
  Stepper,
  SubmitBar,
  Checkbox,
  Divider,
  Badge,
  Tabbar,
  TabbarItem,
  Search,
  ContactCard,
  AddressList,
  AddressEdit,
  CellGroup,
]

export default {
  install(Vue) {
    components.forEach(element => {
      Vue.use(element)
    });

    Vue.prototype.$nanoid = nanoid
    Vue.prototype.$imagePreview = ImagePreview
  }
}