<template>
  <div class="details-root">
    <van-nav-bar
      title="详情"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    >
      <template #right>
        <van-popover
          v-model="showPopover"
          theme="dark"
          trigger="click"
          :actions="actions"
          @select="onSelect"
          placement="bottom-end"
        >
          <template #reference>
            <van-icon name="ellipsis" size="30" />
          </template>
        </van-popover>
      </template>
    </van-nav-bar>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="shareOptions"
      @select="onShareSelect"
    />
    <div class="content">
      <div class="banner">
        <van-swipe ref="swipe">
          <van-swipe-item
            @click="handleSwipeItemClick(index)"
            v-for="(item, index) in detailBanners"
            :key="index"
          >
            <img :src="item" alt="" srcset="" />
          </van-swipe-item>
        </van-swipe>
        <div class="btn-play ignore" @click="handleVideoPlay">
          <van-icon name="play-circle" />
          <span class="time">{{ videoTime }}</span>
        </div>
        <van-popup @close="handlePopupClose" v-model="showVideo">
          <video
            ref="video"
            controls
            preload
            src="https://jvod.300hu.com/vod/product/b1024a62-8c46-464c-8ec7-f57d26cceb38/42f70c8e8bbb442e986874ec556fc882.mp4"
          ></video>
        </van-popup>
      </div>
      <h3 class="price">
        ￥ <span class="price-value">{{ price }}</span>
      </h3>
      <h3 class="detail-name">
        <van-tag type="danger">自营</van-tag>
        <span class="name">{{ proname }}</span>
      </h3>
    </div>
    <div class="details-footer">
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" dot />
        <van-goods-action-icon
          icon="cart-o"
          @click="handleGoToCart"
          text="购物车"
          :badge="totalNum"
        />
        <van-goods-action-icon icon="shop-o" text="店铺" badge="12" />
        <van-goods-action-button
          v-show="flag"
          type="warning"
          text="加入购物车"
          @click="handleAddCart"
        />
        <van-goods-action-button v-show="flag" type="danger" text="立即购买" />
        <van-goods-action-button
          v-show="!flag"
          disabled
          type="danger"
          text="已下架"
        />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import { getDetails } from "@/api/details";
import { cartAdd } from "@/api/cart";
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("user");
const cart = createNamespacedHelpers("cart");

export default {
  data() {
    return {
      showPopover: false,
      actions: [
        { text: "首页", icon: "wap-home-o", name: "home" },
        { text: "购物车", icon: "shopping-cart-o", name: "cart" },
        { text: "分享", icon: "share-o", name: "share" },
      ],
      showShare: false,
      shareOptions: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
      detailBanners: [],
      proname: "",
      price: 0,
      flag: true,
      videoTime: "00′55″",
      showVideo: false,
    };
  },
  created() {
    getDetails(this.$route.params.id)
      .then((res) => {
        this.detailBanners = res.data.banners[0].split(",");
        this.proname = res.data.proname;
        this.price = res.data.originprice;
      })
      .catch((err) => console.error(err));
    // 请求购物车数量数据
    this.getCartListData(this.userid);
  },
  activated() {
    getDetails(this.$route.params.id)
      .then((res) => {
        this.detailBanners = res.data.banners[0].split(",");
        this.proname = res.data.proname;
        this.price = res.data.originprice;
      })
      .catch((err) => console.error(err));
  },
  computed: {
    ...mapState(["userid"]),
    ...cart.mapGetters(["totalNum"]),
  },
  methods: {
    ...cart.mapActions(["getCartListData"]),
    onSelect(item) {
      this.showPopover = false;
      if (item.name === "share") {
        this.showShare = true;
      } else {
        this.$router.push({ name: item.name });
      }
    },
    onShareSelect(option) {
      this.$toast(option.name);
      this.showShare = false;
    },
    handleVideoPlay() {
      this.showVideo = true;
      this.$nextTick(() => {
        console.log(this.$refs.video.duration);
        this.$refs.video.play();
      });
    },
    handlePopupClose() {
      this.$refs.video.currentTime = 0;
      this.$refs.video.pause();
    },
    handleSwipeItemClick(index) {
      this.$imagePreview({
        images: this.detailBanners,
        startPosition: index,
        // 预览图片改变的时候，swipe组件展示的内容对应改变
        onChange: (previewIndex) => {
          this.$refs.swipe.swipeTo(previewIndex);
        },
        closeable: true,
      });
    },
    handleAddCart() {
      // 先校验登录状态
      const token = localStorage.getItem("token");
      if (!token) {
        this.$router.push({
          path: "/login",
          query: { back: this.$route.path },
        });
      } else {
        console.log("add cart");
        cartAdd({
          userid: this.userid,
          proid: this.$route.params.id,
          num: 1,
        }).then((res) => {
          if (res.code === "200") {
            this.getCartListData(this.userid);
            this.$notify({ type: "danger", message: "加入成功" });
          }
          console.log("cartadd", res);
        });
      }
    },
    handleGoToCart() {
      this.$router.push("/cart");
    },
  },
};
</script>

