      border-radius: 18px;
      display: flex;
      justify-content: space-between;
      cursor: pointer;
      opacity: 0.95;
      .van-icon {
        color: #ee0a24;
      }
      .time {
        font-size: 12px;
        color: #262626;
        line-height: 30px;
      }
    }
    video {
      width: 100%;
    }
  }
  .price {
    text-align: left;
