<template>
  <div class="container">
    <van-field
      v-model="sms"
      center
      clearable
      label="短信验证码"
      placeholder="请输入短信验证码"
    >
      <template #button>
        <van-button
          :disabled="msgcodeFlag"
          @click="sendMessage"
          size="small"
          round
          type="danger"
          >{{ msgCodeContent }}</van-button
        >
      </template>
    </van-field>
    <van-button
      @click="handleNext"
      type="danger"
      :disabled="!btnNextFlag"
      round
      block
      >下一步</van-button
    >
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("login");
import { dosendmsgcode, docheckcode } from "@/api/user";

export default {
  data() {
    // 不需要双向绑定的数据放到这里
    this.time = 0;
    return {
      sms: "",
      msgCodeContent: "发送验证码",
      timer: null,
    };
  },
  created() {
    // this.sendMessage();
    // 验证是否刷新了页面还有剩余时间没有跑完，跑完会删除本地存储
    if (localStorage.getItem("time")) {
      const localTime = localStorage.getItem("time").split("_");
      const runTime = new Date().getTime() - localTime[0] * 1;
      if (Math.round(runTime / 1000) < localTime[1] * 1) {
        this.countdown(localTime[1] * 1 - Math.round(runTime / 1000));
      }
    }
  },
  mounted() {
    window.onbeforeunload = this.saveRemainTime.bind(this);
  },
  beforeDestroy() {
    // 页面意外关闭，清除timer
    clearInterval(this.timer);
    this.saveRemainTime();
  },
  computed: {
    ...mapState(["tel"]),
    // 发送验证码按钮可用开关
    msgcodeFlag() {
      return this.msgCodeContent !== "发送验证码";
    },
    // 下一步按钮可用开关
    btnNextFlag() {
      return /^\d{5}$/.test(this.sms);
    },
  },
  methods: {
    handleNext() {
      // 验证验证码是否正确
      docheckcode({
        tel: this.tel,
        telcode: this.sms,
      })
        .then((res) => {
          console.log(res);
          // 验证码无效，提示错误信息
          if (res.code === "10007") {
            this.$notify({ type: "danger", message: res.message });
          } else {
            this.$router.push("/register/step3");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 如果从外部传入了time就表示刷新了页面，还有剩余时间没有跑完
    countdown(time) {
      // 开启之前先清理,保险操作
      clearInterval(this.timer);
      // 发送验证码60s后再次发送, *1 字符串变数字
      this.time = time || 60;
      this.timer = setInterval(() => {
        this.msgCodeContent = --this.time + "s 后再次发送";
        if (this.time === 0) {
          this.msgCodeContent = "发送验证码";
          clearInterval(this.timer);
          // 倒计时完毕清空保存的time剩余值
          localStorage.removeItem("time");
        }
      }, 1000);
    },
    sendMessage() {
      this.countdown();
      const tel = this.tel;
      tel &&
        dosendmsgcode({ tel })
          .then((res) => {
            console.log(res);
            if (res.code === "200") {
              this.$notify({ type: "danger", message: "验证码已发送" });
            } else {
              this.$notify({ type: "danger", message: res.message });
            }
          })
          .catch((error) => {
            console.log(error);
          });
    },
    // 组件销毁之前保存验证码按钮可用的剩余时间（节省公司的钱）
    saveRemainTime() {
      if (this.time !== 0) {
        // 本地保存验证码剩余时间，再次进入页面的时候跟本地时间对比是否跑完
        localStorage.setItem("time", new Date().getTime() + "_" + this.time);
      }
    },
  },
};
</script>

<style lang='scss' scoped>
.container {
  padding: 50px 30px;
  background: #fff;
  box-sizing: border-box;
  .input {
    margin-bottom: 100px;
  }
}
</style>

