import router from '@/router'
import {
  login
} from "@/api/user";

import {
  Dialog,
  Notify
} from 'vant'

export default {
  namespaced: true,
  state: {
    userid: localStorage.getItem('userid') || '',
  },
  mutations: {
    setUserId(state, userid) {
      state.userid = userid
      // 本地保存一下避免开发阶段改代码触发页面刷新就失效
      localStorage.setItem('userid', userid)
    },
  },
  actions: {
    loginAction({
      commit
    }, payload) {
      login({
          loginname: payload.username,
          password: payload.password,
        })
        .then((res) => {
          // res: code: "10010" : 该用户不存在, "10011":"密码错误","200": "登录成功",
          // 200:  data: {token,userid,},
          // 登录接口错误处理, 可以放在api层统一处理
          if (res.code === "10010") {
            Dialog
              .confirm({
                message: res.message,
                confirmButtonText: "立即注册",
              })
              .then(() => {
                router.push("/register");
              })
              .catch(() => {
                // on cancel
              });
          } else {
            // 登录成功保存用户id
            // this.$store.commit("setUserId", res.data.userid);
            commit('setUserId', res.data.userid)
            Notify({
              type: "success",
              onClose: () => {
                // this.$route.query.back &&
                //   this.$router.replace({ path: this.$route.query.back });
                router.go(-1);
              },
              message: res.message,
            });
          }
        })
        .catch((err) => {
          console.error(err);
        });
    }
  },
}