<template>
  <div class="address-container">
    <van-nav-bar
      title="地址"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      default-tag-text="默认"
      @add="onAdd"
      @edit="onEdit"
    />

    <van-popup
      v-model="showAddressEdit"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <van-address-edit
        :area-list="areaList"
        show-postal
        show-delete
        show-set-default
        show-search-result
        :address-info="addressInfo"
        :search-result="searchResult"
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </van-popup>
  </div>
</template>

<script>
import { areaList } from '@vant/area-data'
export default {
  data() {
    return {
      list: [
        {
          id: '1',
          name: '张三',
          tel: '13000000000',
          province: '北京市',
          city: '北京市',
          county: '东城区',
          country: '',
          addressDetail: '杭州市西湖区 黄龙万科中心',
          areaCode: '110101',
          postalCode: '064100'
        },
        {
          id: '2',
          name: '李四',
          tel: '13100000000',
          province: '北京市',
          city: '北京市',
          county: '东城区',
          country: '',
          addressDetail: '杭州市西湖区 黄龙万科中心',
          areaCode: '110101',
          postalCode: '064100'
        },
        {
          id: '3',
          name: '王五',
          tel: '13800000000',
          province: '北京市',
          city: '北京市',
          county: '东城区',
          country: '',
          addressDetail: '杭州市西湖区 黄龙万科中心',
          areaCode: '110101',
          postalCode: '064100',
          isDefault: true
        }
      ],
      // 地址栏数据
      areaList,
      // 控制默认选中的数据
      chosenAddressId: '3',
      // 详细地址检索结果
      searchResult: [],
      // 控制是否展示编辑框
      showAddressEdit: false,
      // 地址编辑的默认数据
      addressInfo: {},
      // 当前编辑的地址数据的 index
      currentIndex: -1
    }
  },
  created() {
    this.list.forEach((item) => {
      item.address =
        item.province + item.city + item.county + item.addressDetail
    })
  },
  methods: {
    onAdd() {
      this.showAddressEdit = true
    },
    onEdit(item, index) {
      this.currentIndex = index
      this.addressInfo = item
      this.showAddressEdit = true
    },
    // 给元素设置address属性用于展示
    formatDate(item) {
      item.address =
        item.province + item.city + item.county + item.addressDetail
      return item
    },
    onSave(content) {
      // 如果编辑内容中设置为默认选中删除之前添加了默认选中属性的该值
      if (content.isDefault) {
        delete this.list.find((item) => item.isDefault === true).isDefault
      }
      // 当currentIndex === -1 表示是新增
      if (this.currentIndex === -1) {
        // 添加id是因为v-model绑定的值（chosenAddressId）匹配元素的id去选中
        this.list.push({
          id: (this.list.length + 1).toString(),
          ...content
        })
      } else {
        // 更新address属性用于展示
        this.list[this.currentIndex] = this.formatDate(content)
      }
      // 临时变量恢复默认值
      this.currentIndex = -1
      this.addressInfo = {}
      this.showAddressEdit = false
      this.$toast('save')
    },
    onDelete() {
      this.$toast('delete')
    },
    onChangeDetail(val) {
      if (val) {
        // 这里要发送请求更新搜索结果
        this.searchResult = [
          {
            name: '黄龙万科中心',
            address: '杭州市西湖区'
          }
        ]
      } else {
        this.searchResult = []
      }
    }
  },
  watch: {
    chosenAddressId(newVal) {
      sessionStorage.setItem(
        'currentAddr',
        JSON.stringify(this.list.find((item) => item.id === newVal))
      )
    }
  }
}
</script>

<style></style>
