<template>
  <div class="box">
    <div class="search-header">
      <van-search
        v-model="query"
        show-action
        clearable
        autofocus
        placeholder="请输入搜索关键词"
        @search="searchHandler"
      >
        <template #left>
          <div class="search-left">
            <van-icon name="arrow-left" size="22" @click="$router.back()" />
          </div>
        </template>
        <template #action>
          <div @click="searchHandler">搜索</div>
        </template>
      </van-search>
    </div>
    <div class="content">
      <SearchList :dataList="hotSearchList" title="搜索历史" />
      <SearchList :dataList="hotSearchList" title="热门搜索" />
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import SearchList from './searchList.vue'
import { getHotWord, getSearchList } from '@/api/search.js'

import { Toast, Tag } from 'vant'
Vue.use(Toast)
Vue.use(Tag)

export default {
  name: 'search-com',
  data() {
    return {
      query: '',
      hotSearchList: []
    }
  },
  components: {
    SearchList
  },
  methods: {
    toLoginHandler() {
      this.$router.back()
    },
    searchHandler() {
      // count limitnum  keyword
      this.query && this.handleSearchItemClick(this.query)
    },
    handleSearchItemClick(keyword) {
      getSearchList({
        keyword
      }).then((res) => {
        console.log(res)
        // localStorage.setItem('searchList', res.data)
      })
    }
  },
  mounted() {
    getHotWord().then((res) => {
      this.hotSearchList = res.data.filter(
        (item) => item.keyword !== '' && item.keyword !== undefined
      )
    })
  }
}
</script>
<style lang="stylus" scoped>
.search-left
  padding: 0 8px 0 0
  color: #323233
  cursor: pointer
  -webkit-user-select: none
  user-select: none
</style>
