<template>
  <div class="container">
    <van-nav-bar
      title="京东登录"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form
      validate-first
      validate-trigger="onChange"
      colon
      @submit="onSubmit"
    >
      <!-- name: 输出的values对应的key -->
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[
          {
            required: true,
            pattern,
            message: '密码格式不正确'
          }
        ]"
      />
      <div style="margin: 16px">
        <van-button round block type="danger" native-type="submit">
          提交
        </van-button>
      </div>
    </van-form>
    <van-cell-group inset title="去注册">
      <van-cell title="快速注册" is-link to="/register" />
    </van-cell-group>
    <!-- <router-link to="/register">快速注册</router-link> -->
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'LoginView',
  data() {
    return {
      username: '17512345666',
      password: '123abcOK!',
      pattern:
        /^\S*(?=\S{6,})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/
    }
  },
  methods: {
    ...mapActions('user', ['loginAction']),
    /**
     * values: { username, password }
     */
    onSubmit(values) {
      console.log('submit', values)
      this.loginAction(values)
    },
    onClickLeft() {
      this.$router.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.van-form {
  padding-top: 25px;
}
</style>
