import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import CommonFooter from "@/components/CommonFooter.vue";
import DetailsView from '../views/DetailsView.vue'
import NotFount from '../views/NotFoundView.vue'
import RegisterView from '../views/RegisterView'
import ChildStep1 from '../views/RegisterView/ChildStep1'
import ChildStep2 from '../views/RegisterView/ChildStep2'
import ChildStep3 from '../views/RegisterView/ChildStep3'
import LoginView from '@/views/LoginView'
import Cart from '@/views/CartView.vue'
import UserView from '@/views/UserView.vue'
import SearchView from '@/views/SearchView'
import OrderView from '@/views/OrderView'
import AddressView from '@/views/AddressView'

Vue.use(VueRouter)
import {
  Notify
} from 'vant';

const routes = [{
    path: '/',
    name: 'home',
    components: {
      default: HomeView,
      footer: CommonFooter
    }
  },
  {
    path: '/details/:id',
    name: 'details',
    component: DetailsView
  },
  {
    path: '/search',
    name: 'search',
    component: SearchView
  },
  {
    path: '/order',
    name: 'order',
    component: OrderView
  },
  {
    path: '/address',
    name: 'address',
    component: AddressView
  },
  {
    path: '/cart',
    name: 'cart',
    meta: {
      // 该页面需要检查登录状态
      needCheckLogin: false
    },
    component: Cart
  },
  {
    path: '/user',
    name: 'user',
    meta: {
      // 该页面需要检查登录状态
      needCheckLogin: false
    },
    component: UserView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "about" */ '../views/AboutView.vue')
  }, {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterView,
    redirect: {
      name: 'step1'
    },
    children: [{
        path: 'step1',
        name: 'step1',
        component: ChildStep1
      },
      {
        path: 'step2',
        name: 'step2',
        component: ChildStep2
      },
      {
        path: 'step3',
        name: 'step3',
        component: ChildStep3
      },
    ]
  },
  { // 路由匹配顺序从上往下匹配，类似oneOf
    path: '**',
    name: 'notfound',
    component: NotFount
  }
]

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    console.log('***', savedPosition)
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})

router.beforeEach((to, from, next) => {
  // to.matched[0],嵌套路由中用于匹配父级路由（子路由全部通过验证）
  if (!to.matched[0].meta.needCheckLogin) {
    next()
  } else {
    const token = localStorage.getItem('token')
    if (!token) {
      Notify({
        type: 'warning',
        message: '请登录后访问',
        onClose: () => {
          // 如何设置
          !token && next({
            name: 'login'
          })
        }
      });
    } else {
      next()
    }
  }
})

export default router