<template>
  <div class="container">
    <van-field class="input" v-model="tel" type="tel" label="请输入手机号" />
    <van-button
      @click="handleNext"
      type="danger"
      :disabled="!disable"
      round
      block
      >下一步</van-button
    >
  </div>
</template>

<script>
import { docheckphone } from "@/api/user";
export default {
  data() {
    return {
      tel: "",
    };
  },
  created() {
    console.log(/^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(this.tel));
  },
  methods: {
    handleNext() {
      docheckphone({ tel: this.tel }).then((res) => {
        console.log(res);
        /**
         * res: {
         *  code: "200"
         *  message: "继续注册"
         * }
         */
        if (res.code === "200") {
          this.$dialog
            .confirm({
              title: "提示",
              message: `将向手机号：${this.tel}发送短线验证码`,
            })
            .then(() => {
              // on confirm
              this.$store.commit("login/setTel", this.tel);
              this.$router.push("/register/step2");
            })
            .catch(() => {
              // on cancel
            });
        } else {
          this.$dialog
            .confirm({
              title: "提示",
              message: "手机号已注册，是否去登录",
            })
            .then(() => {
              // on confirm
              this.$router.go(-1);
            })
            .catch(() => {
              // on cancel
            });
        }
      });
    },
  },
  computed: {
    disable() {
      return /^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(this.tel);
      // return /^1[3-9]\d{9}$/.test(this.tel);
    },
  },
};
</script>

