<template>
  <div class="footer-container">
    <van-tabbar route>
      <van-tabbar-item to="/" icon="home-o">home</van-tabbar-item>
      <van-tabbar-item to="/cart" icon="cart-o">cart</van-tabbar-item>
      <van-tabbar-item :to="userPath" icon="user-o">{{
        userContent
      }}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("user");
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["userid"]),
    userContent() {
      return localStorage.getItem("token") ? "user" : "login";
    },
    userPath() {
      return localStorage.getItem("token") ? "/user" : "/login";
    },
  },
};
</script>

<style lang="scss" scoped>
.van-tabbar {
  height: 100px;
}
</style>
