import fetch from '../service'

// 定义这一层的原因是接口复用的时候比较方便
export function getBannerList() {
  return fetch.request({
    url: "/banner/list",
    method: 'get',
    params: {
      id: 123
    }
  })
}


// 获取秒杀商品数据
export function getSecondKillGoods() {
  return fetch.request({
    url: "/pro/seckilllist",
    method: 'get',
    params: {
      count: 1
    }
  })
}

// 获取商品列表
export function getGoodsList(count) {
  return fetch.request({
    url: "pro/list",
    method: 'get',
    params: {
      count
    }
  })
}