import fetch from '../service'

// 定义这一层的原因是接口复用的时候比较方便
export function getHotWord() {
  return fetch.request({
    url: "/pro/hotword",
    method: 'get'
  })
}

export function getSearchList(data) {
    return fetch.request({
      url: "/pro/search",
      method: 'get',
      params: {
        ...data
      }
    })
  }